import { useStaticQuery, graphql } from 'gatsby';

const useIntro = () => {
  const data = useStaticQuery(
    graphql`
      query allContentfulIntro($locale: String) {
        allContentfulIntro(filter: { node_locale: { eq: $locale } }) {
          edges {
            node {
              name
              description {
                childMarkdownRemark {
                  html
                }
              }
            }
          }
        }
      }
    `
  );
  return data;
};

export default useIntro;
